body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{
  font-family: 'Orbitron', sans-serif;
  background-color: rgba(80,97,138, 0.6);
  text-align: center;
  color: white;
}

h2{
  font-family: 'Orbitron', sans-serif;
  text-align: center;
  color : white;
}

h3{
  font-family: 'Orbitron', sans-serif;
  text-align: center;
  color : black;
}

/* app */

.App{
  width: 1080px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  background-color: rgba(243, 241, 242, 0.8);
}

/* Vista del menu */
.menu{
  width: 100%;
  height: 450px;
  border: 5px solid #000;
  margin: auto;
  background-image: url(/static/media/menu.caab0404.png);
  display: block;
}

.menu_button{
  margin-top: 12px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Orbitron', sans-serif;
  font-size: 20px;

}

#arrows{
  background-size: 60px 50px;
  height: 50px;  
  width: 65px;
  background-color: rgba(162, 162, 162, 0.5);
  position: relative;
  background-image: url("https://cdn-icons-png.flaticon.com/512/724/724802.png");
}

.personaje{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 200px;
  height: 200px;
  position: relative;
}

/* Vista del juego */
.game{
  width: 100%;
  height: 450px;
  border: 5px solid #000;
  margin: auto;
  background-image: url(/static/media/juego.1e4989ba.gif);
  display: none;
}

#back{
  margin-top: 0px;
  top: 0cm;
}

.audio{
  display: none;
}


#ing{
  width: 150px;
  height: 150px;
  background-size: 150px 150px;
  position: relative;
  top: 275px;
  background-image: none;
  margin-left: 0%;
}

.jump{
  -webkit-animation: jump 0.5s linear;
          animation: jump 0.5s linear;
  -webkit-filter: hue-rotate(90deg);
          filter: hue-rotate(90deg);
}
@-webkit-keyframes jump {
  0% {
      top: 250px;
  }
  30% {
      top: 200px;
  }
  50% {
      top: 150px;
  }
  80% {
      top: 200px;
  }
  100% {
      top: 250px;
  }

}
@keyframes jump {
  0% {
      top: 250px;
  }
  30% {
      top: 200px;
  }
  50% {
      top: 150px;
  }
  80% {
      top: 200px;
  }
  100% {
      top: 250px;
  }

}

#personaje_final{
  margin-top: 80px;
}

.prefestejo{
  -webkit-animation: prefestejo 1s linear;
          animation: prefestejo 1s linear;
}

@-webkit-keyframes prefestejo {

  from {
    margin-left: 0%;
  }
  to {
    margin-left: 80%;
  }

}

@keyframes prefestejo {

  from {
    margin-left: 0%;
  }
  to {
    margin-left: 80%;
  }

}

.festejo{
  -webkit-animation: festejo 0.3s;
          animation: festejo 0.3s;
  -webkit-animation-iteration-count: 5;
          animation-iteration-count: 5;
}

@-webkit-keyframes festejo {
  0% {
      bottom: 0px;
  }
  30% {
      bottom: 50px;
  }
  50% {
      bottom: 70px;
  }
  80% {
      bottom: 50px;
  }
  100% {
      bottom: 0px;
  }

}

@keyframes festejo {
  0% {
      bottom: 0px;
  }
  30% {
      bottom: 50px;
  }
  50% {
      bottom: 70px;
  }
  80% {
      bottom: 50px;
  }
  100% {
      bottom: 0px;
  }

}

#personaje_perdio{
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-filter: hue-rotate(180deg);
          filter: hue-rotate(180deg);
  margin-top: 80px;
}


.fall {
  -webkit-animation: fall 0.5s linear;
          animation: fall 0.5s linear;
  -webkit-filter: hue-rotate(180deg);
          filter: hue-rotate(180deg);
}

@-webkit-keyframes fall {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  from{
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  to{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes fall {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  from{
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  to{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

/* vista de preguntas */

.questions{
  width: 100%;
  border: 5px solid #000;
  margin: auto;
  display: none;
}

span {
  font-family: 'Segoe UI Emoji', 'Segoe UI Symbol', 'Symbola', 'Quivira';
  content: "\2764";
  font-size: 25px;
}

.option{

  font-family: 'Orbitron', sans-serif;
  font-size: 15px;
  margin-top: 20px;
  margin-left: 20px;
  margin-right: auto;
  margin-bottom: 20px;

}

.radio{
  -webkit-transform: scale(2);
          transform: scale(2);
  text-align: center;
}

.row{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: left;
}


#progressBar{
  width: 100%;
  position: relative;
  height: 50px;
  color: #0F57A6
}

/* Gameover */

.gameover{
  width: 100%;
  height: 450px;
  border: 5px solid #000;
  margin: auto;
  background-image: url(/static/media/perdio.2e44effe.png);
  display: none;
}

.win{
  width: 100%;
  height: 450px;
  border: 5px solid #000;
  margin: auto;
  background-image: url(/static/media/menu.caab0404.png);
  display: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

